import React, { useState, useEffect } from 'react'
import styles from './table.module.css'
import chev from '../../assets/icons/Chevron-Right.png'
import filter from '../../assets/icons/Filter.png'
import arrowUp from '../../assets/icons/Arrow-Up.png'
import triangle from '../../assets/icons/Triangle-Down.png'

function TableComponent(props) {
  const [data, setData] = useState([])
  useEffect(() => {
    setData(props.data)
    return () => {}
  }, [])

  const buttons = [
    {
      buttonId: 1,
      buttonText: 1,
    },
    {
      buttonId: 2,
      buttonText: 2,
    },
    {
      buttonId: 3,
      buttonText: 3,
    },
    {
      buttonId: 4,
      buttonText: 4,
    },
    {
      buttonId: 6,
      buttonText: 5,
    },
  ]
  const [selectedItem, setSelectedItem] = useState({
    workerId: null,
    client: '',
    title: '',
    worker: '',
    started: '',
    ended: '',
    duration: '',
    status: '',
  })
  const [currentPage, setNewCurrentPage] = useState(1)

  const handlePreviousPage = () => {
    if (currentPage > 1) setNewCurrentPage(currentPage - 1)
  }

  const setCurrentPage = (page) => {
    setNewCurrentPage(page)
  }

  const handleNextPage = () => {
    if (currentPage < buttons.length) setNewCurrentPage(currentPage + 1)
  }

  const selectItem = (id) => {
    setSelectedItem(data[id])
  }

  return (
    <>
      <div className="overflow-auto">
        <table>
          <thead>
            <tr>
              <th className={styles.vaiThClient}>
                CLIENT <img src={filter} alt="FilterIcon1" className="mx-2" />
              </th>
              <th className={styles.vaiThTitle}>
                TITLE <img src={filter} alt="FilterIcon2" className="mx-2" />
              </th>
              <th className={styles.vaiThWorker}>
                WORKER <img src={filter} alt="FilterIcon3" className="mx-2" />
              </th>
              <th className={styles.vaiThStarted}>
                STARTED <img src={filter} alt="FilterIcon4" className="mx-2" />
                <img src={arrowUp} alt="ArrowUpIcon" />
              </th>
              <th className={styles.vaiThEnded}>
                ENDED <img src={filter} alt="FilterIcon5" className="mx-1" />
              </th>
              <th className={styles.vaiThDuration}>
                DURATION <img src={filter} alt="FilterIcon6" className="mx-2" />
              </th>
              <th className={styles.vaiThStatus}>
                STATUS <img src={filter} alt="FilterIcon" className="mx-2" />
              </th>
            </tr>
          </thead>
          <tbody className={styles.vaiTbody}>
            {data.map((item, index) => {
              const successClassName =
                item.status === 'Success' ? styles.vaiSuccessStatus : null
              const runningClassName =
                item.status === 'Running' ? styles.vaiRunningStatus : null
              const failedClassName =
                item.status === 'Failed' ? styles.vaiFailedStatus : null
              const stoppedClassName =
                item.status === 'Stopped' ? styles.vaiStoppedStatus : null
              const queuedClassName =
                item.status === 'Queued' ? styles.vaiQueuedStatus : null
              return (
                <tr
                  className={
                    selectedItem.workerId === item.workerId
                      ? styles.vaiActiveRow
                      : null
                  }
                  key={index}
                  onClick={() => selectItem(index)}
                  style={{ cursor: 'pointer' }}
                >
                  <td>{item.client}</td>
                  <td>{item.title}</td>
                  <td>{item.worker}</td>
                  <td>{item.started}</td>
                  <td>{item.ended}</td>
                  <td>{item.duration}</td>
                  <td>
                    <div
                      className={`${successClassName} ${runningClassName} ${failedClassName} ${stoppedClassName} ${queuedClassName}`}
                    >
                      {item.status}
                    </div>
                  </td>
                </tr>
              )
            })}
          </tbody>
        </table>
      </div>
      <div className={styles.vaiTableFooter}>
        <div className="row">
          <div className="col-12">
            <label className={styles.vaiLabel}>Listing</label>
            <button className={styles.vaiListButton}>
              12{' '}
              <img
                src={triangle}
                alt="Triangle-Icon"
                style={{ marginLeft: '3px', marginBottom: '2px' }}
              />
            </button>
            <label className={styles.vaiLabel}>
              entries, showing 1 to 12 of 342
            </label>

            <div
              className="btn-group float-end"
              role="group"
              aria-label="Basic example"
            >
              <button
                type="button"
                className={styles.vaiPaginationBtn}
                onClick={handlePreviousPage}
              >
                <img
                  src={chev}
                  alt="Paginate-Left"
                  className={styles.vaiChevLeftIcon}
                />
              </button>
              {buttons.map((button, index) => {
                return (
                  <button
                    key={index}
                    type="button"
                    onClick={() => setCurrentPage(button.buttonId)}
                    className={`${styles.vaiPaginationBtn} ${
                      button.buttonId === currentPage
                        ? styles.vaiPaginationActiveBtn
                        : null
                    }`}
                  >
                    {button.buttonText}
                  </button>
                )
              })}
              <button
                type="button"
                className={styles.vaiPaginationBtn}
                onClick={handleNextPage}
              >
                <img
                  src={chev}
                  alt="Paginate-Right"
                  className={styles.vaiChevRightIcon}
                />
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default TableComponent
